export default {
  namespaced: true,
  state: {
    images: [
      {image: 'img/images/Image1.jpg', title: 'Сирень', subtitle: 'На этом рисунке сирень ^^'},
      {image: 'img/images/Image2.jpg', title: 'не Сирень', subtitle: 'На данном рисунке не сирень :)'},
      {image: 'https://krasivosti.pro/uploads/posts/2021-04/thumbs/1617975766_36-p-kotiki-nyashki-38.jpg', title: 'Cat', subtitle: 'Very cute cat :*'},
      {image: 'https://krasivosti.pro/uploads/posts/2021-04/1618117932_13-p-bolshaya-plyushevaya-sobaka-sobaki-krasivo-13.jpg', title: 'Dog', subtitle: 'Very cute dog :*'},

    ],
    selectedImage: 0,
    currentImage: 0,
  },
  mutations: {
    changeImage(state, newImageIndex) {
      state.currentImage = newImageIndex
    },
    selectImage(state, idx) {
      state.selectedImage = idx
    }
  },
  actions: {
    changeImage({commit}, index) {
      commit('selectImage', index)
      setTimeout(() => {
        commit('changeImage', index)
      }, 300)
    }
  }
}
